.toolbar-title {
  display: inline;
  margin: 0;
  padding: 0;
  font-size: .9em;
  font-weight: bold;
  margin-right: 5px;
}

.toolbar input[type="button"] {
  border: 1px solid #eee;
  border-radius: 5px;
  cursor: pointer;
  margin-right: 2px;
  margin-bottom: 2px;
}

.toolbar input[type="button"]:active {
  transform: scale(.95);
}

.toolbar input[type="button"]:not(.active):hover {
  background-color: #eee;
}

.toolbar input[type="button"].active {
  background-color: turquoise;
  border-color: transparent;
}